ges=\"form.errors.get('new_pass')\"\r\n                    ></v-text-field>\r\n\r\n                    <v-text-field\r\n                      v-model=\"form.confirm_pass\"\r\n                      label=\"Confirm Passowrd\"\r\n                      placeholder=\"Enter Confirm Password\"\r\n                      dense\r\n                      required\r\n                      prepend-inner-icon=\"mdi-lock-outline\"\r\n                      :type=\"passwordType2 ? 'text' : 'password'\"\r\n                      :append-icon=\"passwordType2 ? 'mdi-eye' : 'mdi-eye-off'\"\r\n                      @click:append=\"passwordType2 = !passwordType2\"\r\n                      :error-messages=\"form.errors.get('confirm_pass')\"\r\n                    ></v-text-field>\r\n\r\n                    <v-btn block small color=\"primary\" type=\"submit\"\r\n                      >Submit</v-btn\r\n                    >\r\n                  </form>\r\n                </v-card-text>\r\n              </v-card>\r\n            </v-flex>\r\n          </v-layout>\r\n        </v-container>\r\n      </v-main>\r\n    </v-app>\r\n  </div>\r\n</template>\r\n\r\n<script>\r\nimport Form from \"vform\";\r\nexport default {\r\n  data() {\r\n    return {\r\n      passwordType: false,\r\n      passwordType2: false,\r\n      form: new Form({\r\n        new_pass: \"\",\r\n        confirm_pass: \"\",\r\n        owner_id: \"\",\r\n        reset_type: \"\",\r\n        cv_code: \"\",\r\n      }),\r\n    };\r\n  },\r\n\r\n  methods: {\r\n    reset() {\r\n      this.form.post(\"/new_password\").then((response) => {\r\n        Swal.fire(response.data.data, response.data.msg, response.data.status);\r\n        if (response.data.status == \"success\") {\r\n          this.$router.push({\r\n            name: \"Login\",\r\n          });\r\n        }\r\n      });\r\n    },\r\n  },\r\n\r\n  created() {\r\n    console.log(\"created new password\", this.$route.params.owner_id);\r\n    if (!this.$route.params.owner_id) {\r\n  